.menu {
	padding: 0 10px;
	border-bottom: solid 1px #e4e4e4;
	overflow: auto;
	background-color: white;
	color: black;
}

.menu__logo {
	width: 90px;
	float: left;
	padding-left: 30px;
}

.menu__logo a {
	display: inline-block;
	font-size: 20px;
	margin-left: -10px;
	padding: 8px 20px;
}

.menu__container .ant-menu-item {
	padding: 0px 5px;
}

.menu__container .ant-menu-submenu-title {
	padding: 10px 20px;
}

.menu__container .ant-menu-item a,
.menu__container .ant-menu-submenu-title a {
	padding: 10px 15px;
	color: black;
}

.menu__container .ant-menu-horizontal {
	border-bottom: none;
}

.menu__container .menu_left {
	float: left;
	padding-top: 10;
}

.menu__container .menu_rigth {
	float: right;
}

.menu__mobile-title {
	padding: 6px;
	margin: 4px 10px;
	color: black;
	display: none;
}

.menu__mobile-button {
	float: right;
	height: 30px;
	padding: 6px;
	margin: 5px 10px;
	display: none !important; /* use of important to overwrite ant-btn */
	background: #3e91f7;
}

.menu_drawer .ant-drawer-body {
	padding: 0 !important;
}

/* align header of Drawer with header of page */
.menu_drawer .ant-drawer-header {
	padding: 14px 20px !important;
}

@media (max-width: 767px) {
	.menu__mobile-button {
		display: inline-block !important;
	}

	.menu__mobile-title {
		display: inline-block !important;
	}

	.menu_left,
	.menu_rigth {
		display: none;
	}

	.menu__container .ant-menu-item,
	.menu__container .ant-menu-submenu-title {
		padding: 1px 20px;
	}
}
